import React, {useState} from 'react';
import Modal from 'react-modal'
import {Slider} from '@material-ui/core'

const ShopingList = (props)=>{
  // var content = props.data1;
  const [modalIsOpen, setModalIsOpen] = useState(false)
  const [filterIsOpen, setfilterIsOpen] = useState(false)
  const [cart, setCart ] =useState([]);
  const [val, setVal] = useState([0, 100])
const updateRange=(e,data)=>{
setVal(data)
}


  // const addToCart = (procuct)=>{
  //   setCart(
  //     [...cart, procuct]      
  //   )
  //   var source = cart.length;
  //   console.log(source);
  // }

    return(
        <section className="container">
          <div className="row px-4">
                  <div className="col-sm-6 col-xs-6 d-block d-sm-none">
                  <button onClick={()=> setfilterIsOpen(true)} className="btn btn-light"> Sort </button>
                  </div>
                  <div className="col-sm-6 col-xs-6 d-block d-sm-none">
                  <button onClick={()=>setModalIsOpen(true)} className="btn btn-light"> Filter </button>
                  </div>

                    <Modal 
                    isOpen={modalIsOpen} 
                    shouldCloseOnOverLayClick ={false} 
                    onRequestClose = {() => setModalIsOpen(false)}>

                    <h2>Filter</h2>
                    

                    <div style={{minWidth:150, margin:0}}>
                <div className="container">
                    <div className="row alg">
                        <div className="col-sm-3 col-xs-3">
                            <p className="firstVal">{val[0]+100} <span className="endval">{val[1]*100}</span></p>
                        </div>
                    </div>                  
                    
                </div>
                <Slider
                value={val}
                onChange={updateRange}
                />
                <div className="text-center mt-4"> 
                  <h3>Price</h3>
                </div>
            </div>

                    <div className="btn-group22">
                      <button onClick={()=> setModalIsOpen(false)}>Close</button>
                      <button>Apply</button>
                    </div>
                    </Modal>      

                    <Modal 
                    isOpen={filterIsOpen} 
                    shouldCloseOnOverLayClick ={false} 
                    onRequestClose = {() => setfilterIsOpen(false)}>

                    <h2>Sort</h2>
                    <di>
<div className="custom-control custom-radio">
  <div style={{marginTop:'10px', fontSize: '2rem'}}>
  <input type="radio" className="custom-control-input" id="hightolow" name="defaultExampleRadios"/>
  <label className="custom-control-label" for="hightolow">Price --High to Low</label>

  </div>

  <div style={{marginTop:'10px', fontSize: '2rem'}}>
  <input type="radio" className="custom-control-input" id="lowtohigh" name="defaultExampleRadios"/>
  <label className="custom-control-label" for="lowtohigh">Price --Low to High</label>
    </div>
    
    <div style={{marginTop:'10px', fontSize: '2rem'}}>
    <input type="radio" className="custom-control-input" id="defaultUnchecked2" name="defaultExampleRadios"/>
  <label className="custom-control-label" for="defaultUnchecked2">Discount</label>
    </div>


  
</div>
                    </di>
                    <div className="btn-group22">
                    <button onClick={()=> setfilterIsOpen(false)}>Close</button>
                      <button>Apply</button>
                    </div>
                    </Modal>      
          </div>
            <div className="row p-4">
              {
                props.data1.map((item, i)=>                
                 { return <div className="col-lg-3 col-md-4 col-sm-6 col-xs-6" key={i}>
                    {console.log(item)}
                      <div className="card">
                      <img className="card-img-top" src={item.image} alt="Card image cap"/>
                      <div className="card-body">
                        <h5 className="card-title">{item.name}</h5>
              <p className="card-text"><span className="price">{item.price.actual}</span> <span className="realPrice">{item.price.display}</span> <span className="offerprice">{item.discount}</span></p>
                      <a href="#" className="btn btn-primary primaryBtn" onClick={(hunt)=>props.addToCart(item)}>Add to Cart</a>
                      </div>
                      </div>
                  </div>
                  })
              }
              
            </div>            
        </section>
    );
}

export default ShopingList