import React from 'react';
import CartItem from './CartItems';
const Cart = (props)=>{

    
    return(
        <div className="container">
            <div className="row">
<div className="col-md-9">
        <div className="row" id="cartItem">
            <div className="col-md-12">
{[props.selectItem].map((product, i)=>{ return (

                <CartItem pr={product} key={i}/>
       )}
    )}  
    </div>  
 </div>
</div>


               


                    <div className="col-md-3 col-sm-12 px-5 adjestma">
                            <div className="row ">
                                <div className="col-md-12 col-sm-12 bdr-rs">
                                    <div className="bb-1">
                                            <h3>Price Details</h3>
                                    </div>
                                    <div className="tpd">
                                        <p><span className="priced-i">Price (i item) :</span> <span style={{marginLeft:'6.5rem', fontWeight: 600}} className="priced-p">₹500</span></p>
                                        <p><span className="priced-i">Discount (i item) :</span> <span style={{marginLeft:'4rem', fontWeight: 600}} className="priced-p">₹200 </span></p>
                                    </div>
                                    <div>
                                        <h2 className="to-pay">Total Payable <span className="totalcost" style={{marginLeft:'2.3rem'}}> ₹700 </span></h2>
                                    </div>
                                </div>
                            </div>
                    </div>
            </div>
        </div>
    );
}

export default Cart