import React from 'react';
import Aside from './Aside';
import ShopingSection from './ShopingSection';

const SectionMain = (props)=>{
    return(
        <section className="container px-5 ht-100">
            <div className="row pad-top">
                <div className="col-md-2 px-4 bdr-rt ht-90 d-none d-sm-block">
                <Aside/>
                </div>
                
                <div className="col-md-10 col-sm-12 px-4 ht-90">
                <ShopingSection data={props.data} sortEvent={props.sortEvent} cart={props.cart} addToCart={props.addToCart}/>
                </div>
            </div>
           
        </section>
    );
}

export default SectionMain;