import React from 'react';
import ShopingList from './ShopingList';
import SortSection from './SortSection';

const ShopingSection = (props)=>{
    return(
        <section className="container">
            <div className="row">
                <div className="col-md-12 d-none d-sm-block">
                    <SortSection />
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <ShopingList data1={props.data} sortEvent={props.sortEvent} cart={props.cart} iii={props.iii} addToCart={props.addToCart}/>
                </div>
            </div>
        </section>
    );
}

export default ShopingSection;